import { request } from '../utils/requestToJs';

const prefix = '/api/finance';

export const getPaymentData = ({
  yearEduId,
  semesterId
} = {}) =>
  request({
    prefix,
    url: `/Payment/GetPaymentData`,
    method: 'GET',
    params: { yearEduId, semesterId },
    data: {},
  });

export const getPaymentHistory = () =>
  request({
    prefix,
    url: `/Payment/GetPaymentHistory`,
    method: 'GET',
    params: {},
    data: {},
  });

export const getPaymentDetailHistory = ({
  receiptId
} = {}) =>
  request({
    prefix,
    url: `/Payment/GetPaymentHistory/${receiptId}`,
    method: 'GET',
    params: {},
    data: {},
  });

export const createPaymentRequest = (data) =>
  request({
    prefix,
    url: `/Payment/CreatePaymentRequest`,
    method: 'POST',
    params: {},
    data,
  });



