import React, { useState, useEffect } from 'react';
import { Card, Row, Col, Table, Descriptions } from 'antd';
import { getPaymentDetailHistory } from '../../../services/payment';
import { dateToString, moneyToString } from '../../../utils/formatUtil';
import './detail.scss';
import CardTitle from '../../../components/common/CardTitle/index';

export default (props = {}) => {
  const { id } = props.match && props.match.params

  const [paymentInfo, setPaymentInfo] = useState({});
  const [isFetching, setIsFetching] = useState(false)

  const callGetPaymentDetailHistory = async () => {
    setIsFetching(true)
    const [result, err] = await getPaymentDetailHistory({ receiptId: id })
    setIsFetching(false)

    if (err) {
      return;
    }

    setPaymentInfo(result.data)
  }

  useEffect(() => {
    callGetPaymentDetailHistory()
  }, [])

  const columns = [
    {
      title: 'Nội dung',
      dataIndex: 'label',
    },
    {
      title: 'Số tín chỉ',
      dataIndex: 'noOfCredit',
      width: '150px',
      align: 'right'
    },
    {
      title: 'Đơn giá',
      dataIndex: 'unitPrice',
      width: '150px',
      align: 'right',
      render: (text) => text && moneyToString(text)
    },
    {
      title: 'Tổng tiền',
      dataIndex: 'totalAmount',
      width: '150px',
      align: 'right',
      render: (text) => text && moneyToString(text)
    },
    {
      title: 'Thanh toán',
      dataIndex: 'paidAmount',
      width: '150px',
      align: 'right',
      render: (text) => text && moneyToString(text)
    },
  ]

  return (
    <React.Fragment>
      <Card className="payment-history-detail">
        <CardTitle title="Chi tiết thanh toán" />
        <Row>
          <Col span={24}>
            <Descriptions layout="vertical" column={5} className="payment-summary">
              <Descriptions.Item label="Năm học">{paymentInfo.yearEduLabel}</Descriptions.Item>
              <Descriptions.Item label="Học kỳ">{paymentInfo.semesterLabel}</Descriptions.Item>
              <Descriptions.Item label="Lần thanh toán">{paymentInfo.paymentTimeLabel}</Descriptions.Item>
              <Descriptions.Item label="Ngày thanh toán">{dateToString(paymentInfo.paymentDate)}</Descriptions.Item>
              <Descriptions.Item label="Hình thức">{paymentInfo.paymentTypeLabel}</Descriptions.Item>
              <Descriptions.Item label="Người thu">{paymentInfo.casherName}</Descriptions.Item>
              <Descriptions.Item label="Nội dung">{paymentInfo.paymentContent}</Descriptions.Item>
              <Descriptions.Item label="Giảm">{moneyToString(paymentInfo.reducedAmt)}</Descriptions.Item>
              <Descriptions.Item label="Tổng tiền">{moneyToString(paymentInfo.amount)}</Descriptions.Item>
              <Descriptions.Item label="Còn lại">{moneyToString(paymentInfo.remainAmt)}</Descriptions.Item>
            </Descriptions>
            <br />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table
              bordered
              columns={columns}
              dataSource={paymentInfo.paymentDetails}
              pagination={false}
              loading={isFetching}
            />
          </Col>
        </Row>
      </Card >
    </React.Fragment>
  )
}


